/*--------------------------------------------------------------
# Navigation
--------------------------------------------------------------*/

/* Links */

a {
  color: $color__link;

  // &:visited {
  // 	color: $color__link-visited;
  //}
  &:hover,
  &:focus,
  &:active {
    color: $color__link-hover;
  }
  &:focus {
    outline: thin dotted;
  }
  &:hover,
  &:active {
    outline: 0;
  }
}

/* Menus */

ul {
  // display: none;
  list-style: none;
  margin: 0;
  padding-left: 10;

  ul {
    box-shadow: 0 3px 3px rgba(0, 0, 0, 0.2);
    float: left;
    position: absolute;
    top: $base__space-unit;
    left: -999rem;
    z-index: 99999;

    ul {
      left: -999rem;
      top: 0;
    }

    li {
      &:hover > ul,
      &.focus > ul {
        left: 100%;
      }
    }

    a {
      width: 200px;
    }

    :hover > a,
    .focus > a {
    }

    a:hover,
    a.focus {
    }
  }

  li:hover > ul,
  li.focus > ul {
    left: auto;
  }
}
li {
  float: left;
  position: relative;
  // padding-left: 10px;
  // padding-right: 10px;
  min-width: 80px;
  text-align: center;

  &:hover > a,
  &.focus > a {
  }
}
a {
  display: block;
  text-decoration: none;
}

.current_page_item > a,
.current-menu-item > a,
.current_page_ancestor > a {
}

/* Small menu. */
.menu-toggle,
.main-navigation.toggled ul {
  display: block;
}

@media screen and (min-width: 768px) {
  .menu-toggle {
    display: none;
  }
  .main-navigation ul {
    display: block;
  }
}

.comment-navigation,
.posts-navigation,
.post-navigation {
  .site-main & {
    margin: 0 0 $base__space-unit;
    overflow: hidden;
  }

  .nav-previous {
    float: left;
    width: 50%;
  }

  .nav-next {
    float: right;
    text-align: right;
    width: 50%;
  }
}

.home .main-navigation ul {
  flex-wrap: nowrap;
}

.search-submit,
.search-field {
  display: none;
}

.header-search .menu-toggle {
  color: white;
}
